/* layout
------------------------------------------------------------*/
body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.4rem;
  min-width: 1200px;
  line-height: 1.5;
}

@media only screen and (max-width: 767px) {
  body {
    min-width: 320px;
  }
}

.c-news__space {
  margin-bottom: 30px;
}

main {
  padding: 0px;
  margin: 0px;
}

.c-link {
  height: 45px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 40px 0px 70px;
}

.c-link:before {
  content: "";
  display: block;
  clear: both;
}

.c-link__anchor {
  height: 45px;
  width: 300px;
  text-align: center;
  line-height: 45px;
  color: #fff;
  background: url("../img/anchor_link1.png") no-repeat center;
}

.c-link__anchor:not(:last-child) {
  width: 300px;
  height: 100%;
  float: left;
  margin-right: 45px;
}

.c-link__anchor:after {
  margin-right: 0px;
}

.c-link p:before {
  font-family: "Font Awesome 5 Free";
  content: "\f078";
  display: inline-block;
  padding-right: 5px;
  vertical-align: middle;
  font-weight: 900;
}

.c-anchor1 {
  display: block;
}

.c-anchor1__list, .c-anchor2__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.c-anchor1 h4 {
  font-size: 1.8rem;
  font-weight: bold;
}

.c-anchor1 h4:after {
  content: "";
  margin: 5px 0px 25px;
  display: block;
  height: 2px;
  background: url("../img/link.png") repeat-x center;
}

.c-anchor1 p {
  width: 100%;
  margin-bottom: 15px;
}

.c-anchor1__item {
  margin-bottom: 23px;
  width: 300px;
  margin-right: 35px;
}

.c-anchor1__item:nth-child(3n+0) {
  margin-right: 0px;
}

.c-anchor1__item__img {
  width: 300px;
  height: 185px;
}

.c-anchor1__item__info p {
  text-align: justify;
}

.c-anchor__text {
  margin: 15px 0px 20px;
  padding: 6px 22px;
  display: inline-block;
  background-color: #a3d39c;
  text-align: justify;
}

.c-anchor--orange {
  background-color: #ffdfdf !important;
}

.c-anchor--yellow {
  background-color: #fff799 !important;
}

.c-anchor2 h4 {
  font-size: 1.8rem;
  font-weight: bold;
}

.c-anchor2 h4:after {
  content: "";
  margin: 5px 0px 25px;
  display: block;
  height: 2px;
  background: url("../img/link.png") repeat-x center;
}

.c-anchor2__item {
  margin-bottom: 23px;
  width: 235px;
  margin-right: 20px;
}

.c-anchor2__item:nth-child(4n+0) {
  margin-right: 0px;
}

